@import '~scss/variables';
@import '~scss/custom-properties';

reservation-rules {
  $rr-role-width: 71px;

  .rr-container {
    display: flex;
    flex-wrap: wrap;
    padding-left: $rr-role-width;
    z-index: 0;

    & > * {
      flex-basis: 100%;
    }
  }

  rr-rule {
    flex: 1 1 100%;
    background-color: #fff;

    .rr-role {
      position: absolute;
      right: 100%;
      top: 0;
      bottom: 0;
      width: $rr-role-width;
      z-index: -1;
      text-transform: uppercase;

      .ff-label {
        font-size: .9rem;
        font-weight: bold;
      }
    }
    ff-header.rr-role {
      bottom: -2px; // inner fake ff-header for bottom line
    }

    ff-btn.ng-scope {
      margin: 0 .333rem;
      z-index: 3;
    }

    ff-header.ff-spin.ff-spin::before { // spin in rule header
      left: auto;
      right: 2px;
      margin-top: -1rem;
      z-index: 1;
    }

    ff-header .ff-hint {
      height: 15px;
      line-height: 15px;
    }
  }

  .rr-calendar ff-header {
    padding-bottom: 0;

    .ff-control {
      min-height: 2.1rem;
    }

    ff-text .ff-control {
      border: none;
    }

    cell {
      padding: 0;
    }

    &.ff-spin::before {
      right: 9px;
    }
  }
}
